export function fixturesHasErrored(state = false, action) {
    switch (action.type) {
        case 'FIXTURES_HAS_ERRORED':
            return action.hasErrored;

        default:
            return state;
    }
}

export function fixturesIsLoading(state = false, action) {
    switch (action.type) {
        case 'FIXTURES_IS_LOADING':
            return action.isLoading;

        default:
            return state;
    }
}

export function fixtures(state = [], action) {
    switch (action.type) {
        case 'FIXTURES_FETCH_DATA_SUCCESS':
            return action.fixtures;

        default:
            return state;
    }
}



