export function fixturesHasErrored(bool) {
    return {
        type: 'FIXTURES_HAS_ERRORED',
        hasErrored: bool
    };
}

export function fixturesIsLoading(bool) {
    return {
        type: 'FIXTURES_IS_LOADING',
        isLoading: bool
    };
}

export function fixturesFetchDataSuccess(fixtures) {
    return {
        type: 'FIXTURES_FETCH_DATA_SUCCESS',
        fixtures
    };
}

export function fixturesFetchData(url) {
    return (dispatch) => {
        dispatch(fixturesIsLoading(true));

        fetch(url)
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }

                dispatch(fixturesIsLoading(false));

                return response;
            })
            .then((response) => response.json())
            .then((fixtures) => dispatch(fixturesFetchDataSuccess(fixtures)))
            .catch(() => dispatch(fixturesHasErrored(true)));
    };
}



