export function standingsHasErrored(bool) {
    return {
        type: 'STANDINGS_HAS_ERRORED',
        hasErrored: bool
    };
}

export function standingsIsLoading(bool) {
    return {
        type: 'STANDINGS_IS_LOADING',
        isLoading: bool
    };
}

export function standingsFetchDataSuccess(standings) {
    return {
        type: 'STANDINGS_FETCH_DATA_SUCCESS',
        standings
    };
}

export function standingsFetchData(url) {
    return (dispatch) => {
        dispatch(standingsIsLoading(true));

        fetch(url)
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }

                dispatch(standingsIsLoading(false));

                return response;
            })
            .then((response) => response.json())
            .then((standings) => dispatch(standingsFetchDataSuccess(standings)))
            .catch(() => dispatch(standingsHasErrored(true)));
    };
}



