import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { fixturesFetchData } from '../actions/fixtures';
import config from '../../config';

class Fixtures extends Component {

    render() {
      if (this.props.hasErrored) {
        return <p>Sorry! There was an error loading the standings</p>;
        }

        if (this.props.isLoading) {
          return <p>Loading…</p>;
          }
        let fixtures = this.props.fixtures.map((item, index) => {
          return item.fixtures.map((x, y)=>{
            return <li key={y}>{y===0?(<div className="fm-fixtures__date"><span className="fm-fixtures__date__label">{item.nepDate}</span></div>):''}

                <div className="fm-fixture">
                  <div className="fm-fixture__time"></div>
                  <div className="fm-fixture__team fm-fixture__team--home">
                    <p className="fm-fixture__team__name">{x.home}</p>
                    <div className="fm-fixture__team__logo">
                      <div className="fm-image fm-image--loaded"><img className="fm-image__wrapper fm-team-logo fm-team-logo--small" src={x.homeLogo} /></div>
                    </div>
                  </div>
                  <div className="fm-fixture__status">
                    <span className="match-status">{x.time}</span>
                  </div>
                  <div className="fm-fixture__team fm-fixture__team--away">
                    <div className="fm-fixture__team__logo">
                      <div className="fm-image fm-image--loaded"><img className="fm-image__wrapper fm-team-logo fm-team-logo--small" src={x.awayLogo} /></div>
                    </div>
                    <p className="fm-fixture__team__name">{x.away}</p>
                  </div>
                </div>
            </li>;
          });
        });

        return (
          <section className="fm-fixtures" id="league-results-section">
            <h3 className="fm-league__title">{this.props.league.title + ' - आउने खेलहरु'}</h3>
            <ul className="fm-fixtures__list" id="kj-fixtures-list">
              {fixtures}
            </ul>
          </section>
        );
    }
}

Fixtures.propTypes = {
    fetchData: PropTypes.func.isRequired,
    fixtures: PropTypes.array.isRequired,
    hasErrored: PropTypes.bool.isRequired,
    isLoading: PropTypes.bool.isRequired
};

const mapStateToProps = (state) => {
    return {
        fixtures: state.fixtures,
        hasErrored: state.fixturesHasErrored,
        isLoading: state.fixturesIsLoading
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        fetchData: (url) => dispatch(fixturesFetchData(url))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Fixtures);



