export function standingsHasErrored(state = false, action) {
    switch (action.type) {
        case 'STANDINGS_HAS_ERRORED':
            return action.hasErrored;

        default:
            return state;
    }
}

export function standingsIsLoading(state = false, action) {
    switch (action.type) {
        case 'STANDINGS_IS_LOADING':
            return action.isLoading;

        default:
            return state;
    }
}

export function standings(state = [], action) {
    switch (action.type) {
        case 'STANDINGS_FETCH_DATA_SUCCESS':
            return action.standings;

        default:
            return state;
    }
}



