import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import config from '../../config';

class Results extends Component {

    render() {
      if (this.props.hasErrored) {
        return <p>Sorry! There was an error loading the standings</p>;
        }

        if (this.props.isLoading) {
          return <p>Loading…</p>;
          }

        let results = this.props.results.map((item, index) => {
          return item.games.map((x, y)=>{
            return <li key={y}>{y===0?(<div className="fm-fixtures__date"><span className="fm-fixtures__date__label">{item.npDate}</span></div>):''}

                <div className="fm-fixture">
                  <div className="fm-fixture__time"></div>
                  <div className="fm-fixture__team fm-fixture__team--home">
                    <p className="fm-fixture__team__name">{x.home.team}</p>
                    <div className="fm-fixture__team__logo">
                      <div className="fm-image fm-image--loaded"><img className="fm-image__wrapper fm-team-logo fm-team-logo--small" src={x.home.logo} /></div>
                    </div>
                  </div>
                  <div className="fm-fixture__status">
                    <span className="match-status">{x.home.scoreNp}:{x.away.scoreNp}</span>
                  </div>
                  <div className="fm-fixture__team fm-fixture__team--away">
                    <div className="fm-fixture__team__logo">
                      <div className="fm-image fm-image--loaded"><img className="fm-image__wrapper fm-team-logo fm-team-logo--small" src={x.away.logo} /></div>
                    </div>
                    <p className="fm-fixture__team__name">{x.away.team}</p>
                  </div>
                </div>
            </li>;
          });
        });

        return (
          <section className="fm-fixtures" id="league-results-section">
            <h3 className="fm-league__title">{this.props.league.title + ' - नतिजाहरु'}</h3>
            <ul className="fm-fixtures__list" id="kj-fixtures-list">
              {results}
            </ul>
          </section>
        );
    }
}

Results.propTypes = {
    fetchData: PropTypes.func.isRequired,
    results: PropTypes.array.isRequired,
    hasErrored: PropTypes.bool.isRequired,
    isLoading: PropTypes.bool.isRequired
};

const mapStateToProps = (state) => {
    return {
        results: state.results,
        hasErrored: state.resultsHasErrored,
        isLoading: state.resultsIsLoading
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        fetchData: (url) => dispatch(resultsFetchData(url))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Results);



