import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { standingsFetchData } from '../actions/standings';
import { fixturesFetchData } from '../actions/fixtures';
import { resultsFetchData } from '../actions/results';
import footmob from '../../styles/fotmob-styles.css';
import style from '../../styles/style.css';
import config from '../../config';

import LeagueTable from '../components/table';
import Fixtures from '../components/fixtures.js';
import Results from '../components/results.js';

class League extends Component {

  constructor(props) {
    super(props);
    this.state = {
      league: ''
    };
  }

  componentDidMount() {
    this.setState({ league: 'epl' });
    this.props.fetchData(`https://api.kaslejityo.com/epl/standings`);
  }

  handleChange(league){
    this.setState({league: league});
    this.props.fetchData(`https://api.kaslejityo.com/${league}/standings`);
  }

  render() {
    let styles = {
      columnStyles: {
        margin: '10px',
        width: '32%'
      },
    };

    let npLeague = config.leagues[this.state.league || 'epl'].np;
    let league = {
      class: 'block-title',
      title: npLeague ,
      league: this.state.league
    };

    let buttons = ['epl', 'laliga', 'siriea'].map((item, index) => {
      return (
        <button onClick={this.handleChange.bind(this, item)} key={item} style={{marginRight: '10px'}}>
          {config.leagues[item].np}
        </button>
      );
    });
    return (
      <div>
        {buttons}
        <LeagueTable league = {league} standings={this.props.standings} /> <br/>
        {/* <Results league = {league} results={this.props.fixtures}/>
        <Fixtures league = {league} fixtures={this.props.fixtures}/><br/> */}
      </div>
    );
  }
}

League.propTypes = {
  fetchData: PropTypes.func.isRequired,
  standings: PropTypes.array.isRequired,
  fixtures: PropTypes.array.isRequired,
  results: PropTypes.array.isRequired,
  hasErrored: PropTypes.bool.isRequired,
  isLoading: PropTypes.bool.isRequired
};

const mapStateToProps = (state) => {
  return {
    standings: state.standings,
    fixtures: state.fixtures,
    results: state.results,
    hasErrored: state.standingsHasErrored,
    isLoading: state.standingsIsLoading
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchData: (url) => {
      dispatch(standingsFetchData(url));
      //dispatch(fixturesFetchData(url.replace('standings', 'fixtures')));
      //dispatch(resultsFetchData(url.replace('table', 'results')));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(League);



