import { combineReducers } from 'redux';
import { standings, standingsHasErrored, standingsIsLoading } from './standings';
import { fixtures, fixturesHasErrored, fixturesIsLoading } from './fixtures';
import { results, resultsHasErrored, resultsIsLoading } from './results';

export default combineReducers({
    standings,
    standingsHasErrored,
    standingsIsLoading,
    fixtures,
    fixturesHasErrored,
    fixturesIsLoading,
    results,
    resultsHasErrored,
    resultsIsLoading
});



