import React, { Component, PropTypes } from 'react';

class LeagueTable extends Component {

    render() {

      if (this.props.hasErrored) {
        return <p>Sorry! There was an error loading the standings</p>;
        }

        if (this.props.isLoading) {
          return <p>Loading…</p>;
          }

        return (
          <section>
            <h3 className="fm-league__title">{this.props.league.title + ' - अंक तालिका'}</h3>
            <table className="fm-table">
              <thead>
                <tr className="fm-table__row" style={{fontWeight: 'bold'}}>
                  <td className="fm-table__position">स्थान</td>
                  <td className="fm-table__team">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;टिम</td>
                  <td className="fm-table__teamvalue">खेल</td>
                  <td className="fm-table__teamvalue">+/-</td>
                  <td className="fm-table__points">अंक</td>
                </tr>
                </thead>
                <tbody>
                  {this.props.standings.map((item, index) => (
                    <tr className="fm-table__row" key={index}>
                      <td className="fm-table__position">{item.position}</td>
                      <td className="fm-table__team">
                        <div className="fm-image fm-table__team__logo fm-image--loaded"><img src={item.logo} className="fm-image__wrapper fm-team-logo fm-team-logo--small"/></div>{item.name}</td>
                        <td className="fm-table__teamvalue">{item.played}</td>
                        <td className="fm-table__teamvalue">{item.gd}</td>
                        <td className="fm-table__points">{item.points}</td>
                      </tr>
                  ))}
              </tbody>
            </table>
          </section>
        );
    }
}
export default LeagueTable;



